import {ADMIN_ROUTE, BASKET_ROUTE, DEVICE_ROUTE, LOGIN_ROUTE, REGISTRATION_ROUTE} from "./utils/consts";
import Shop from "./pages/Shop";
import Auth from "./pages/Auth";
import DevicePage from "./pages/DevicePage";
import Baasket from "./pages/Baasket";
import admin from "./pages/Admin";

export const authRoutes = [
    {
        path: ADMIN_ROUTE,
        Component: admin
    },
    {
        path: BASKET_ROUTE,
        Component: Baasket
    }
]
//////////////////////////////////////////////
///////////////////////////////////////////'

export const publicRoutes = [

    {
        path: Shop,
        Component: Shop
    },
    {
        path: LOGIN_ROUTE,
        Component: Auth
    }, {
        path: REGISTRATION_ROUTE,
        Component: Auth
    },
    {
        path: DEVICE_ROUTE + '/id',
        Component: DevicePage
    }


]
